.tag-item-name {
  padding-left: 3px; 
  width: 100%;
  overflow: hidden;
}

.tag-item-token {
  overflow: hidden;
  align-items: center;
  background-color: #efefef;
  border-radius: 2px;
  min-height: 20px;
  display: flex;
  flex-direction: row;
  margin: 6px 6px 6px; 
}

.tag-item-token button {
  padding: 0;
  border: none; 
}