.sidebar-item {
  position: relative;
  display: flex;
  padding-left: 5%;
  padding-right: 3%; 
  font-size: 14px;
}
.sidebar-item-bottom-border {
  border-bottom: 1px solid #e4e6eb; 
}

.sidebar-item-top-border {
  border-top: 1px solid #e4e6eb; 
}
.sidebar-item-wide {
  padding-top: 10px;
  padding-bottom: 10px;
  min-height: 50px;
  height: 50px; }

.sidebar-item-narrow {
 padding-bottom: 10px;
}

 .sidebar-item label {
  padding-left: 10px; 
}
.sidebar-item  label {
  padding-bottom: 4px;
  padding-left: 0px; }